/**
 *  ajax, axios url path 
 *  @module $ui/utils/ajax
 *  @author   <chenhuachun@xdh.net.cn>
 *
 */

import pathToRegex from 'path-to-regexp'
import isPlainObject from 'lodash/isPlainObject'
import axios from './axios'
import {getHost} from './url'
import {get, save, LOCAL, SESSION} from './storage'
import bus from './bus'
import {guid} from './util'
import globalConfig from '../config'


// url
const pathToRegexCaches = {}

// 
const Caches = {}


/**
 * ajax 
 * @const
 * @type {{url: null, method: string, params: null, data: null}}
 * @property {string} url  - , 
 * @property {string} method - get
 * @property {object} params - path: /api/user/:id, url, null
 * @property {object} data - get head deletepost, null,  Object/URLSearchParams/FormData
 * @property {Object|boolean} cache 
 * @property {boolean} socket webSocket
 */
const defaultConfig = {
  url: null,
  method: 'get',
  params: null, // path: /api/user/:id
  query: null, // url 
  data: null, // get head deletepost
  cache: null, // boolean/Object, Object: {local, session, key} local:localStoragesession:SessionStorage keykeyurl+query
  socket: false // boolean/Object, Object:{name, channel}  websocket
}


/**
 * key, url
 * @private
 * @param {string} url url
 * @param {string} method 
 * @param {object} query url
 * @param {object} data 
 * @return {string}
 */

function createCacheKey(url, method, query, data) {
  const keys = [
    url,
    method,
    isPlainObject(query) ? JSON.stringify(query) : query?.toString(),
    isPlainObject(data) ? JSON.stringify(query) : data?.toString()]
  return encodeURIComponent(keys.join(','))
}

export function parseOptions(opts = {}) {
  const config = {...defaultConfig, ...opts}

  // url
  // url  /api/path/:id
  // urlurl
  let compileCache = pathToRegexCaches[config.url]
  const host = getHost(config.url || '')
  if (!compileCache) {
    // hosthost
    compileCache = pathToRegexCaches[config.url] = pathToRegex.compile(config.url.replace(host, ''))
  }

  // urlhost
  const url = host + compileCache(config.params)
  const method = (config.type || config.method || 'get').toLowerCase()
  const params = ['get', 'head', 'delete'].includes(method)
    ? Object.assign({}, config.data, config.query)
    : config.query
  const cache = config.cache ? {
    key: createCacheKey(url, method, params, config.data),
    ...config.cache
  } : null

  const socket = config.socket ? {
    channel: guid(),
    name: config.socket.name || '__async__'
  } : null


  clean(config, ['method', 'type', 'query', 'params', 'cache', 'socket'])

  return {
    config: {
      ...config,
      url,
      method,
      params
    },
    cache,
    socket
  }

}

/**
 * 
 * @private
 * @param {Object} res 
 * @param {object} cache 
 * @param {Storage} storage 
 * @param {function} resolve
 * @param {function} reject
 */
function processData(res, cache, storage, resolve, reject) {
  if (res.data) {
    const {keys, statusCode} = globalConfig
    const {code, data} = keys
    const config = res.config || {}
    //  json 
    if (config.responseType !== 'json') {
      resolve(res.data)
      return
    }
    if (String(res.data[code]) === String(statusCode.success)) {
      (cache && cache.key) && saveCache(cache.key, res.data[data], storage)
      resolve(res.data[data])
    } else {
      reject(res.data)
    }
  } else {
    reject(res)
  }
}

/**
 *
 * ajaxPromiseajaxjsoncode AJAX_SUCCESS   reject
 * @private
 * @returns {Promise} Promise 
 */
function createPromise({config, cache, socket}) {
  let cacheData, storage = null
  if (cache && cache.key) {
    storage = cache.local ? LOCAL : (cache.session ? SESSION : null)
    cacheData = getCache(cache.key, storage)
  }
  return new Promise((resolve, reject) => {
    if (cacheData) {
      resolve(cacheData)
      return
    }

    // websocket
    if (socket) {
      // websocket
      config.params = {
        ...config.params,
        // websocket
        [socket.name]: socket.channel
      }

      // websocket
      // 
      bus.$once(socket.channel, res => {
        processData(res, cache, storage, resolve, reject)
      })
    }
    axios(config).then(res => {
      // websockethttp
      if (socket) return
      processData(res, cache, storage, resolve, reject)
    }).catch(e => reject(e))

  })

}


/**
 * 
 * @private
 * @param {string} key key
 * @param {Storage} storage localStorage/sessionStorage/null
 * @return {String|Object|Array}
 */
function getCache(key, storage) {
  return storage ? get(key, storage) : Caches[key]
}

/**
 * 
 * @private
 * @param {string} key key
 * @param {Object|Array} data 
 * @param {Storage} storage localStorage/sessionStorage/null
 */
function saveCache(key, data, storage) {
  if (storage) {
    save(key, data, storage)
  } else {
    Caches[key] = data
  }
}

/**
 * 
 * @private
 * @param object
 * @param props
 */
function clean(object, props = []) {
  props.forEach(name => {
    delete object[name]
  })
}

/**
 * ajax 
 * @export
 * @param {object} options - ajax. []{@link module:utils/ajax~defaultConfig}
 * @returns {Promise} promise
 *
 *  @example
 *
 *  // 
 *  ajax({
 *    url: '/api/users'
 *  })
 *  .then(res => {
 *    // to do something...
 *  })
 *  .catch(e => {
 *    // to do something...
 *  })
 *
 *  @example
 *
 *  // url path
 *  ajax({
 *    method: 'get',
 *    url: '/api/users/:id',
 *    params: {
 *      id: '123'
 *    }
 *  })
 *
 *  @example
 *
 *  // 
 *  ajax({
 *    url: '/api/users',
 *    method: 'post',
 *    data: {
 *      name: 'kenny',
 *      password: '123456'
 *    }
 *  })
 *
 *  @example
 *  // 
 *  ajax({
 *    url: '/api/users/',
 *    data: {
 *      page: 1,
 *      limit: 10
 *    },
 *    headers: {
 *      'Content-type': 'application/x-www-form-urlencoded'
 *    }
 *  })
 *
 *  @example
 *
 *  // 
 *  ajax({
 *    url: '/api/user',
 *    cache: true
 *  })
 *
 * // LocalStorage
 *  ajax({
 *    url: '/api/user',
 *    cache: {
 *      local: true
 *    }
 *  })
 *
 * // SessionStorage
 *  ajax({
 *    url: '/api/user',
 *    cache: {
 *      session: true
 *    }
 *  })
 *
 * // key
 *  ajax({
 *    url: '/api/user',
 *    cache: {
 *      local: true,
 *      key: 'cachekey'
 *    }
 *  })
 *
 *  @example httpwebsocketwebsocket
 *  ajax({
 *    url: '/api/user',
 *    socket: true
 *  })
 *
 *  // webscoket 
 *  ws.on('FwzxSyncCall', function (res) {
 *        if (res && res.NotifyId) {
 *         bus.$emit(res.NotifyId, responseData({data: res.Data}))
 *       }
 *     })
 *
 */
export default function (options = {}) {
  // 
  const opts = {...defaultConfig, ...options}
  // url
  if (!opts.url) {
    throw new Error('ajax url is required!')
  }

  const config = parseOptions(opts)
  return createPromise(config)
}


/**
 * axios , 
 * @param {Function} transformer 
 * @param {string} method 
 * @param {object} postData 
 * @param {object} params url
 * @returns {Function}
 */
export function transformHandler(transformer, method, postData, params) {
  return function (data) {
    const json = typeof data === 'string' ? JSON.parse(data) : data
    return transformer(json, method, postData, params)
  }
}
