/**
 * Base,  el-input 
 * @module $ui/components/my-input
 */

import {Input} from 'element-ui'
import Base from '../Base'

/**
 * 
 * @member slots
 * @property {string} prefix  textarea=false 
 * @property {string} suffix  textarea=false 
 * @property {string} prepend  textarea=false 
 * @property {string} append  textarea=false 
 */
export default {
  name: 'MyInput',
  mixins: [Base],
  components: {
    Input
  },
  /**
   * 
   * @member props
   * @property {boolean} [textarea] 
   */
  props: {
    value: String,
    textarea: Boolean
  },
  computed: {
    type() {
      return this.textarea ? 'textarea' : 'text'
    }
  },
  render() {
    const vnode = (
      <Input ref="comp"
             class="my-input"
             {...{props: this.innerProps, on: this.$listeners, attrs: this.innerProps}}
             type={this.type}
             vModel={this.fieldValue}>
        {this.createSlots(['prefix', 'suffix', 'prepend', 'append'])}
      </Input>
    )
    
    return this.renderComponent(vnode)
  },
  mounted() {
    this.extendMethods(this.$refs.comp, ['focus', 'blur', 'select'])
  }
  
}
