<template>
  <div :class="classes">
    <template v-if="loading">
      <div class="my-skeleton__header" v-if="avatar">
        <span class="my-skeleton__avatar" :class="avatarClasses"></span>
      </div>
      <div class="my-skeleton__content" v-if="title || paragraph">
        <h3 v-if="title" class="my-skeleton__title" :style="titleStyle"></h3>
        <ul v-if="paragraph" class="my-skeleton__paragraph">
          <li v-for="(item,index) in rows" :key="index" :style="item"></li>
        </ul>
      </div>
    </template>
    <slot v-else></slot>
  </div>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-skeleton
   */

  /**
   * 
   * @member slots
   * @property {string} default 
   */
  export default {
    name: 'MySkeleton',
    /**
     * 
     * @member props
     * @property {boolean} [active=false] 
     * @property {boolean} [loading=false]  true 
     * @property {Boolean|Object} [avatar=false] , 
     * @property {string} [avatar.size]  defaultlargesmall
     * @property {string} [avatar.shape]  circlesquare
     * @property {boolean|object} [paragraph=true] , 
     * @property {string} [paragraph.width=60%] 
     * @property {number} [paragraph.rows=3] 
     * @property {Boolean|Object} [title=true] ,
     * @property {string} [title.width=50%] 
     *
     */
    props: {
      // 
      active: {
        type: Boolean
      },
      // 
      avatar: {
        type: [Boolean, Object],
        default: false
      },
      //  true 
      loading: Boolean,
      // 
      paragraph: {
        type: [Boolean, Object],
        default: true
      },
      // 
      title: {
        type: [Boolean, Object],
        default: true
      }
    },
    computed: {
      classes() {
        return ['my-skeleton', {
          'is-active': this.active,
          'my-skeleton--with-avatar': !!this.avatar
        }]
      },
      avatarClasses() {
        const {size, shape} = this.getAvatarProps() || {}
        return {
          [`is-avatar-${size}`]: !!size,
          [`is-avatar-${shape}`]: !!shape
        }
      },
      titleStyle() {
        const titleProps = this.getTitleProps() || {}
        return {
          width: titleProps.width
        }
      },
      rows() {
        const paragraphProps = this.getParagraphProps() || {}
        const rows = []
        for (let i = 0, len = paragraphProps.rows; i < len; i++) {
          rows.push(i === len - 1 ? {width: paragraphProps.width} : {})
        }
        return rows
      }
    },
    methods: {
      getAvatarProps() {
        return this.avatar
          ? {
            size: 'large', // largesmall
            shape: 'circle',
            ...this.avatar
          }
          : null
      },
      getTitleProps() {
        return this.title
          ? {
            width: '50%',
            ...this.title
          }
          : null
      },
      getParagraphProps() {
        return this.paragraph
          ? {
            width: '61%',
            rows: 3,
            ...this.paragraph
          }
          : null
      }
    }
  }
</script>
