<template>
  <div class="my-corner-mark" :class="classes">
    <div class="my-corner-mark__wrapper">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-corner-mark
   */

  /**
   * 
   * @member slots
   * @property {string} default 
   */
  export default {
    name: 'MyCornerMark',
    /**
     * 
     * @member props
     * @property {string} [type=primary] 'primary', 'success', 'warning', 'danger'
     * @property {string} [size]  'large', '', 'small',
     * @property {string} [target] 
     *
     */
    props: {
      type: {
        type: String,
        default: 'primary',
        validator(val) {
          return ['primary', 'success', 'warning', 'danger'].includes(val)
        }
      },
      size: {
        type: String,
        default: '',
        validator(val) {
          return ['large', '', 'small'].includes(val)
        }
      },
      target: [String, HTMLElement]
    },
    computed: {
      classes() {
        return {
          [`is-${this.size}`]: !!this.size,
          [`is-${this.type}`]: !!this.type
        }
      }
    },
    mounted() {
      this.targetEl = typeof this.target === 'string'
        ? document.querySelector(this.target)
        : this.target
      if (this.targetEl) {
        this.targetEl.appendChild(this.$el)
      }
    },
    beforeDestroy() {
      if (this.targetEl) {
        this.$el.parentNode.removeChild(this.$el)
      }
    }
  }
</script>

