
  <template>
  <div class="my-resize" :class="classes" :style="styles">
    <slot></slot>
    <div v-if="helper && resizing" class="my-resize__helper" :style="helperStyle"></div>
    <my-drag v-if="!disabled && (!axis || axis==='h')"
             ref="ctrlH"
             class="my-resize__ctrl-h"
             axis="h"
             :origin="getOrigin"
             @start="handleStart"
             @drag="handleDragH"
             @stop="handleStop">

    </my-drag>
    <my-drag v-if="!disabled && (!axis || axis==='v')"
             ref="ctrlV"
             class="my-resize__ctrl-v"
             axis="v"
             :origin="getOrigin"
             @start="handleStart"
             @drag="handleDragV"
             @stop="handleStop">
    </my-drag>
    <my-drag v-if="!disabled && !axis"
             ref="ctrl"
             class="my-resize__ctrl"
             :origin="getOrigin"
             @start="handleStart"
             @drag="handleDrag"
             @stop="handleStop">
      <slot name="icon"></slot>
    </my-drag>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     
     import {MyDrag as __MyDrag__} from '$ui';
     
     
     
     /**
   * MyResize , dragdrag
   * @module $ui/components/my-resize
   *
   */

  import {MyDrag} from '$ui'
  import {setStyle} from 'element-ui/lib/utils/dom'

  /**
   * 
   * @member slots
   * @property {string} default 
   * @property {string} icon 
   */
  export default {
    name: 'MyResize',
    components: {
      MyDrag
    },
    /**
     * 
     * @member props
     * @property {String} [axis] : v h 
     * @property {Boolean} [disabled] 
     * @property {Boolean} [animate] , helpertrue
     * @property {Boolean} [helper] 
     * @property {Number} [minWidth=0] 
     * @property {Number} [minHeight=0] 
     * @property {Number} [maxWidth=Infinity] 
     * @property {Number} [maxHeight=Infinity] 
     */
    props: {
      // : v h 
      axis: {
        type: String,
        default: '',
        validator(val) {
          return ['', 'v', 'h'].includes(val)
        }
      },
      // 
      disabled: Boolean,

      // , helpertrue
      animate: Boolean,

      // 
      helper: Boolean,

      minWidth: {
        type: Number,
        default: 0
      },
      minHeight: {
        type: Number,
        default: 0
      },
      maxWidth: {
        type: Number,
        default: Infinity
      },
      maxHeight: {
        type: Number,
        default: Infinity
      }
    },
    data() {
      return {
        width: null,
        height: null,
        helperWidth: null,
        helperHeight: null,
        resizing: false
      }
    },
    computed: {
      classes() {
        return {
          'is-disabled': this.disabled,
          'is-resizing': this.resizing,
          'is-animate': this.animate,
          [`is-axis-${this.axis}`]: !!this.axis,
          'is-axis-both': !this.axis
        }
      },
      helperStyle() {
        return {
          width: `${this.helperWidth}px`,
          height: `${this.helperHeight}px`
        }
      },
      styles() {
        return {
          width: this.width ? `${this.width}px` : null,
          height: this.height ? `${this.height}px` : null
        }
      }
    },
    methods: {
      getOrigin() {
        return this.$el
      },
      lockSize(w, h) {
        if (w !== null) {
          if (w < this.minWidth) {
            this.helperWidth = this.minWidth
          }
          if (w > this.maxWidth) {
            this.helperWidth = this.maxWidth
          }
        }
        if (h !== null) {
          if (h < this.minHeight) {
            this.helperHeight = this.minHeight
          }
          if (h > this.maxHeight) {
            this.helperHeight = this.maxHeight
          }
        }
        if (!this.helper) {
          this.applySize()
        }

      },
      applySize() {
        this.width = this.helperWidth
        this.height = this.helperHeight
      },
      clearCtrlStyle() {
        if (this.$refs.ctrlH) {
          setStyle(this.$refs.ctrlH.$el, 'left', '')
        }
        if (this.$refs.ctrlV) {
          setStyle(this.$refs.ctrlV.$el, 'top', '')
        }
        if (this.$refs.ctrl) {
          setStyle(this.$refs.ctrl.$el, {
            left: '',
            top: ''
          })
        }
      },
      handleStart(vm) {
        this.resizing = true
        /**
         * 
         * @event start
         * @param {VueComponent} resize MyResize
         */
        this.$emit('start', this)

      },
      handleDrag(vm) {
        this.helperWidth = vm.x + 20
        this.helperHeight = vm.y + 20
        this.lockSize(this.helperWidth, this.helperHeight)
        /**
         * 
         * @event resize
         * @param {VueComponent} resize MyResize
         */
        this.$emit('resize', this, vm)
      },
      handleDragH(vm) {
        this.helperWidth = vm.x + 10
        this.lockSize(this.helperWidth, this.helperHeight)
        this.$emit('resize', this, vm)
      },
      handleDragV(vm) {
        this.helperHeight = vm.y + 10
        this.lockSize(this.helperWidth, this.helperHeight)
        this.$emit('resize', this, vm)
      },
      handleStop(vm) {
        this.resizing = false
        this.applySize()
        this.clearCtrlStyle()
        /**
         * 
         * @event stop
         * @param {VueComponent} resize MyResize
         */
        this.$emit('stop', this)
      }
    }
  }
     
     __vue__.use(__MyDrag__)
    
  </script>


  