
  <template>
  <div>
    <el-divider content-position="left">Crud</el-divider>
    <my-crud :filter="{collapsible:true, labelWidth:'80px'}"
             ref="crud"
             :columns="columns"
             :adapter="adapter"
             :table-options="{rowKey:'id'}"
             :form-options="{labelWidth:'80px'}"
    >
      <template v-slot:title>
        
      </template>

      <template v-slot:toolbar>
        <el-button type="primary" size="small" @click="openAddDialog"></el-button>
        <el-button type="danger" size="small" @click="batchRemove"></el-button>
      </template>

      <template v-slot:filter>
        <my-input label="" name="user"></my-input>
        <my-select label="" name="type" collapsible></my-select>
        <my-input label="" name="title" collapsible></my-input>
        <my-range label="" mode="date" name="range" collapsible></my-range>
      </template>

      <template v-slot:form>
        <div>
          <my-input label="" name="name" :rules="{required:true}"></my-input>
          <my-date-picker label="" name="date" :props="{valueFormat:'yyyy-MM-dd'}"></my-date-picker>
        </div>
      </template>
    </my-crud>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Button as __Button__,Divider as __Divider__} from 'element-ui';
     import {MyCrud as __MyCrud__,MyDatePicker as __MyDatePicker__,MyInput as __MyInput__,MyRange as __MyRange__,MySelect as __MySelect__} from '$ui';
     
     
     
     import MockForExample from '$my/code/mixin/mock-for-example'
  import {MyCrud} from '$ui'

  export default {
    mixins: [MockForExample],
    data() {
      return {
        adapter: MyCrud.adapter.call(this, 'mockForExample'),
        tags: [
          {
            label: '1',
            value: 1
          },
          {
            label: '1',
            value: 2
          },
          {
            label: '1',
            value: 3
          },
          {
            label: '1',
            value: 4
          },
          {
            label: '1',
            value: 5
          },
          {
            label: '1',
            value: 6
          }
        ],
        columns: [
          {type: 'selection'},
          {
            type: 'index',
            label: '#'
          },
          {
            label: '',
            prop: 'name'
          },
          {
            label: '',
            prop: 'id'
          },
          {
            label: '',
            prop: 'date'
          },
          {
            type: 'handle',
            label: '',
            width: 200,
            handles: [
              {view: true},
              {edit: true},
              {remove: true}
            ]
          }
        ]
      }
    },
    methods: {
      openAddDialog() {
        this.$refs.crud.openAddDialog()
      },
      batchRemove() {
        this.$refs.crud.batchRemove()
      }
    }
  }
     __vue__.use(__Button__);
__vue__.use(__Divider__)
     __vue__.use(__MyCrud__);
__vue__.use(__MyDatePicker__);
__vue__.use(__MyInput__);
__vue__.use(__MyRange__);
__vue__.use(__MySelect__)
    
  </script>

  