<template>
  <div :class="classes">
    <span v-if="$slots.prefix"><slot name="prefix"></slot></span>
    <span class="my-date__value"><slot :value="value"
                                       :displayValue="displayValue"
                                       :dayjs="dayjs">{{displayValue}}</slot></span>
    <span v-if="$slots.suffix"><slot name="suffix"></slot></span>
  </div>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-date
   */
  import dayjs from 'dayjs'
  import relativeTime from 'dayjs/plugin/relativeTime'
  import 'dayjs/locale/zh-cn'

  dayjs.extend(relativeTime)
  dayjs.locale('zh-cn')

  /**
   * 
   * @member slots
   * @property {string} default value displayValue  dayjs 
   * @property {string} prefix 
   * @property {string} suffix 
   */
  export default {
    name: 'MyDate',
    /**
     * 
     * @member props
     * @property {string|number|Date} [value] 
     * @property {string|number|Date} [defaultValue] , value defaultValuenull
     * @property {string} [format=YYYY-MM-DD HH:mm:ss]  Y M D H m s Z A
     * @property {boolean} [relative=false] 
     * @property {string} [type] 'primary', 'success', 'warning', 'danger', 'info'
     * @property {boolean} [tick=false] , 
     */
    props: {

      // 
      value: [String, Number, Date],

      // 
      defaultValue: {
        type: [String, Number, Date],
        default() {
          return null
        }
      },

      //  
      format: {
        type: String,
        default: 'YYYY-MM-DD HH:mm:ss'
      },

      // 
      relative: Boolean,

      // 
      type: {
        type: String,
        validator(val) {
          return ['', 'primary', 'success', 'warning', 'danger', 'info'].includes(val)
        }
      },

      // , 
      tick: Boolean
    },
    data() {
      this.timerId = null
      return {
        /**
         * dayjs
         * @member dayjs
         * @type {Dayjs}
         */
        dayjs: null
      }
    },
    computed: {
      classes() {
        return {
          'my-date': true,
          [`is-${this.type}`]: !!this.type
        }
      },
      displayValue() {
        if (!this.dayjs) return ''
        if (this.relative) {
          return this.dayjs.fromNow()
        }
        return this.dayjs.format(this.format)
      }
    },
    watch: {
      value: {
        immediate: true,
        handler() {
          this.stop()
          this.init()
        }
      },
      tick(val) {
        val ? this.start() : this.stop()
      }
    },
    methods: {
      init() {
        let instance = dayjs(this.value)
        if (!instance.isValid()) {
          instance = this.defaultValue ? dayjs(this.defaultValue) : null
        }
        this.dayjs = instance
        this.tick ? this.start() : this.stop()
      },
      start() {
        if (!this.dayjs) return
        clearInterval(this.timerId)
        this.timerId = setInterval(() => {
          this.dayjs = this.dayjs.add(1, 'second')
        }, 1000)
      },
      stop() {
        clearInterval(this.timerId)
        this.timerId = null
      }
    },
    beforeDestroy() {
      this.stop()
      this.dayjs = null
    }
  }
</script>
