<template>
  <div :class="classes" v-on="$listeners">
    <span v-if="prefix||$slots.prefix" :class="supClass"><slot name="prefix">{{prefix}}</slot></span>
    <span class="my-number__value" ref="container">
      <slot :value="value" :displayValue="displayValue">{{displayValue}}</slot>
    </span>
    <span v-if="suffix||$slots.suffix" :class="supClass"><slot name="suffix">{{suffix}}</slot></span>
    <span v-if="trend||$slots.trend"
          class="my-number__trend"
          :class="trendClasses">
      <slot name="trend" :trend="trend"><i :class="trendIcon"></i></slot>
    </span>
  </div>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-number
   */
  import CountUp from './CountUp'

  const defaultCountUp = {
    auto: true, // 
    startVal: 0, // 0
    decimalPlaces: 0, // 0
    duration: 2, // 2
    useEasing: true, // 
    useGrouping: true, // 10,00010000separator
    separator: ',' // ,
  }
  /**
   * 
   * @member slots
   * @property {string} default value displayValue 
   * @property {string} prefix 
   * @property {string} suffix 
   * @property {string} trend trend 
   */

  export default {
    name: 'MyNumber',
    mixins: [CountUp],
    /**
     * 
     * @member props
     * @property {number|string} [value] 
     * @property {number} [defaultValue=0] valuedefaultValue,  defaultValuenull
     * @property {boolean|Object} [countUp] CountUp
     * @property {boolean} [countUp.auto=true] 
     * @property {number} [countUp.startVal=0] 0
     * @property {number} [countUp.duration=2] 2
     * @property {boolean} [countUp.useEasing=true] 
     * @property {string} [trend] , 'up', 'down', '-'
     * @property {number} [precision=0] 
     * @property {string} [separator=,] ,
     * @property {string} [prefix] 
     * @property {string} [suffix] 
     * @property {boolean} [percentage] , value=0.2,  20%
     * @property {string} [type] ,  'primary', 'success', 'warning', 'danger', 'info'
     * @property {boolean} [sup]   
     */
    props: {
      // 
      value: [Number, String],

      // 
      defaultValue: {
        type: [Number, String],
        default: 0
      },
      // CountUp
      countUp: {
        type: [Boolean, Object]
      },
      // 
      trend: {
        type: String,
        validator(val) {
          return ['up', 'down', '-'].includes(val)
        }
      },
      // 
      precision: {
        type: Number,
        default: 0
      },
      // 
      separator: {
        type: String,
        default: ','
      },
      // 
      prefix: {
        type: String
      },
      // 
      suffix: String,

      // 
      percentage: Boolean,

      // 
      type: {
        type: String,
        default: '',
        validator(val) {
          return ['', 'primary', 'success', 'warning', 'danger', 'info'].includes(val)
        }
      },
      //   
      sup: Boolean

    },
    computed: {
      displayValue() {
        if (!this.isNumber(this.value)) {
          return this.defaultValue ? this.getPercent(this.defaultValue) : ''
        }

        if (this.percentage) {
          return this.getPercent(this.value)
        }

        return this.format(this.value, this.precision, this.separator)
      },
      trendIcon() {
        if (!this.trend) return null
        const classes = {
          up: 'el-icon-top',
          down: 'el-icon-bottom',
          '-': 'el-icon-minus'
        }
        return classes[this.trend]
      },
      trendClasses() {
        if (!this.trend) return
        if (this.trend === '-') {
          return 'is-default'
        }
        return `is-${this.trend}`
      },
      classes() {
        return {
          'my-number': true,
          'is-pointer': this.$listeners.click,
          [`is-${this.type}`]: !!this.type
        }
      },
      supClass() {
        return {
          'my-number__sup': this.sup
        }
      },
      countUpOptions() {
        if (!this.countUp) return null
        return {
          ...defaultCountUp,
          ...this.countUp,
          separator: this.separator,
          decimalPlaces: this.precision
        }
      }
    },
    methods: {
      isNumber(n) {
        const val = Number.parseFloat(n)
        return !Number.isNaN(val) && Number.isFinite(val)
      },
      getPercent(val) {
        return this.percentage
          ? `${(Number.parseFloat(val) * 100).toFixed(this.precision)}%`
          : val
      },
      /**
       * 
       * @param val
       * @param n
       * @param separator
       * @return {string}
       */
      format(val, n, separator) {
        const s = Number.parseFloat(String(val).replace(/[^\d.-]/g, '')).toFixed(n) + '';
        const l = s.split('.')[0].split('').reverse()
        const r = s.split('.')[1]
        let t = ''
        for (let i = 0; i < l.length; i++) {
          t += l[i] + ((i + 1) % 3 === 0 && (i + 1) !== l.length ? `${separator}` : '');
        }
        return t.split('').reverse().join('') + (r ? `.${r}` : '')
      }
    }
  }
</script>

