<template>
  <div :class="classes">
    <template v-if="tag">
      <Tag v-if="multiple && checkAll"
           type="primary"
           :size="size"
           class="my-tag-select__tag"
           :effect="isAll ? 'dark' : 'light'"
           @click="handleCheckAll"
      >
      </Tag>
      <Tag v-for="(item,index) in optionsComputed"
           :key="index"
           type="primary"
           :class="{'my-tag-select__tag':true, 'is-disabled':item.disabled, 'is-checked':isChecked(item)}"
           :size="size"
           :effect="isChecked(item) ? 'dark' : 'light'"
           @click="handleChecked(item)">
        <slot :item="item" :index="index">{{item.label}}</slot>
      </Tag>
    </template>
    <template v-else>
      <span v-if="multiple && checkAll" :class="{'my-tag-select__tag':true, 'is-checked':isAll}"
            @click="handleCheckAll"></span>
      <span v-for="(item, index) in optionsComputed"
            :key="index"
            @click="handleChecked(item)"
            :class="{'my-tag-select__tag':true, 'is-disabled':item.disabled, 'is-checked':isChecked(item)}">
     <slot :item="item" :index="index">{{item.label}}</slot>
    </span>
    </template>
    <a v-if="collapsible" class="my-tag-select__expand" @click="toggleCollapse">
      <template v-if="currentCollapsed">
         <i class="el-icon-arrow-down"></i>
      </template>
      <template v-else>
         <i class="el-icon-arrow-up"></i>
      </template>
    </a>
  </div>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-form/src/common/TagSelect
   */
  import {Tag} from 'element-ui'
  import {isEqual} from '$ui/utils/util'

  export default {
    components: {
      Tag
    },
    /**
     * 
     * @member props
     * @property {*} [value] , 
     * @property {Array} [options]  [{label, value, disabled}]
     * @property {Object} [keyMap]  {label, value, disabled}
     * @property {Boolean} [collapsible=false] 
     * @property {Boolean} [collapsed=true] collapsible=true
     * @property {Boolean} [checkAll] multiple=true
     * @property {Boolean} [multiple] 
     * @property {Boolean} [tag] tag
     * @property {string} [size] 'medium', 'small', 'mini', ''
     * @property {boolean} [disabled] 
     * @property {boolean} [readonly] 
     * @property {number} [min=0] checkAllcheckAll
     * @property {number} [max=Infinity] checkAllcheckAll
     */
    props: {
      // 
      value: [String, Number, Array],
      // 
      options: {
        type: Array,
        default() {
          return []
        }
      },
      // 
      keyMap: {
        type: Object,
        default() {
          return {
            label: 'label',
            disabled: 'disabled',
            value: 'value'
          }
        }
      },
      // 
      collapsible: {
        type: Boolean,
        default: false
      },
      // collapsible=true
      collapsed: {
        type: Boolean,
        default: true
      },
      // multiple=true
      checkAll: Boolean,
      // 
      multiple: Boolean,

      // tag
      tag: Boolean,

      // 
      size: {
        type: String,
        default: '',
        validator(val) {
          return ['medium', 'small', 'mini', ''].includes(val)
        }
      },
      // 
      disabled: Boolean,
      // 
      readonly: Boolean,
      // 
      min: {
        type: Number,
        default: 0
      },
      // 
      max: {
        type: Number,
        default: Infinity
      }
    },
    data() {
      return {
        currentCollapsed: true,
        checked: [],
        isAll: false
      }
    },
    computed: {
      optionsComputed() {
        const {value, label, disabled} = this.keyMap
        return this.options.map(opt => {
          if (typeof opt === 'object') {
            return {
              label: opt[label],
              disabled: opt[disabled],
              value: opt[value] || opt[label]
            }
          } else {
            return {
              label: opt,
              disabled: false,
              value: opt
            }
          }
        })
      },
      classes() {
        return {
          'my-tag-select': true,
          [`my-tag-select--${this.size}`]: !!this.size,
          'my-tag-select--text': !this.tag,
          'is-collapsed': this.collapsible && this.currentCollapsed,
          'is-disabled': this.disabled,
          'is-readonly': this.readonly
        }
      }
    },
    watch: {
      collapsed: {
        immediate: true,
        handler(val) {
          this.currentCollapsed = (this.collapsible && val)
        }
      },
      value: {
        immediate: true,
        handler(val, old) {
          if (isEqual(val, old)) return
          if (this.multiple) {
            this.checked = val ? [].concat(val) : []
          } else {
            this.checked = val
          }
        }
      },
      checked(val) {
        this.$emit('input', val)
        /**
         * 
         * @event change
         * @param {*} value
         */
        this.$emit('change', val)
        this.isAll = val.length === this.optionsComputed.filter(n => !n.disabled).length
      }
    },
    methods: {
      isChecked(item) {
        return this.multiple
          ? !!this.checked.find(v => v === item.value)
          : (this.checked === item.value)
      },
      handleChecked(item) {
        if (item.disabled || this.readonly || this.disabled) return
        if (this.multiple) {
          const length = this.checked.length
          if (this.isChecked(item)) {
            if (length > this.min) {
              this.checked = this.checked.filter(n => n !== item.value)
            }
          } else {
            if (length < this.max) {
              this.checked.push(item.value)
            }
          }
        } else {
          this.checked = item.value
        }
      },
      toggleCollapse() {
        this.currentCollapsed = !this.currentCollapsed
        /**
         * 
         * @event collapsed
         * @param {boolean} collapsed 
         */
        this.$emit('collapsed', this.currentCollapsed)
      },
      handleCheckAll() {
        if (this.readonly || this.disabled) return
        if (this.isAll) {
          this.checked = []
        } else {
          this.checked = this.optionsComputed.filter(n => !n.disabled).map(n => n.value)
        }
      }
    }
  }
</script>

