
  <template>
  <div class="my-table-column-filter">
    <el-popover
      placement="bottom"
      popper-class="my-table-column-filter__popper"
      trigger="click">
      <i slot="reference" class="my-table-column-filter__trigger el-icon-more"></i>
      <el-checkbox-group v-if="checkboxList.length" v-model="currentValue">
        <el-checkbox v-for="(col,index) in checkboxList"
                     :key="col.prop||index"
                     :disabled="col.display==='always'"
                     :label="col.prop">{{col.label}}
        </el-checkbox>
      </el-checkbox-group>
      <div v-else></div>
    </el-popover>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Checkbox as __Checkbox__,CheckboxGroup as __CheckboxGroup__,Popover as __Popover__} from 'element-ui';
     
     
     
     
     export default {
    props: {
      columns: {
        type: Array,
        default() {
          return []
        }
      },
      value: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {
        currentValue: this.value || []
      }
    },
    computed: {
      checkboxList() {
        return this.columns.filter(col => !!col.prop && !col.type)
      }
    },
    watch: {
      value: {
        immediate: true,
        handler(val) {
          this.currentValue = val
        }
      },
      currentValue(val) {
        this.$emit('input', val)
      }
    }
  }
     __vue__.use(__Checkbox__);
__vue__.use(__CheckboxGroup__);
__vue__.use(__Popover__)
     
    
  </script>


  