<template>
  <Form class="my-form"
        ref="elForm"
        v-bind="$attrs"
        v-on="$listeners"
        @keyup.native.enter="submit"
        :model="currentModel">
    <slot></slot>
    <FormItem label=" " v-if="footer" class="my-form__footer" :class="footerClass">
      <slot name="footer">
        <Button v-if="submitText"
                type="primary"
                @click="submit"
                :loading="prevent && submitting">{{(prevent && submitting) ? submittingText : submitText}}
        </Button>
        <Button v-if="resetText" :disabled="prevent && submitting" type="default" @click="reset">{{resetText}}</Button>
        <slot name="actions"></slot>
        <Button v-if="collapsible"
                :disabled="prevent && submitting"
                type="text"
                @click="toggleCollapsed"
                class="my-form__collapse">
         <span>
           {{currentCollapsed ? '': ''}}
           <i
             :class="currentCollapsed ? 'el-icon-arrow-down' : 'el-icon-arrow-up'"></i>
           </span>
        </Button>
      </slot>

    </FormItem>
  </Form>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-form
   */
  import {Form, FormItem, Button} from 'element-ui'
  import {cloneDeep, isEqual} from '$ui/utils/util'

  // const _set = require('lodash/set')

  /**
   * 
   * @member slots
   * @property {string} default 
   * @property {string} actions 
   * @property {string} footer/
   */
  export default {
    name: 'MyForm',
    components: {
      Form,
      FormItem,
      Button
    },
    provide() {
      return {
        myForm: this
      }
    },
    /**
     * 
     * @member props
     * @property {Object} [model] 
     * @property {string} [itemWidth] css
     * @property {boolean} [footer=true] false/
     * @property {string} [footerAlign] 'left', 'right', 'center'
     * @property {String|Boolean} [submitText=] false 
     * @property {String} [submittingText=...] ,  prevent=true 
     * @property {Function} [onSubmit] Promise
     * @property {string} [resetText] false 
     * @property {Boolean} [resetSubmitSuccess=false] 
     * @property {Boolean} [collapsible] 
     * @property {boolean} [collapsed=true] collapsible=true
     * @property {string} [collapseEffect] transition  name
     * @property {boolean} [resetCollapsed] 
     * @property {boolean} [footerBlock] 
     * @property {boolean} [footerExpandBlock=true] ,
     * @property {boolean} [footerFloat=false] 
     * @property {Function} [loader] , Promise, loader
     * @property {Object} [dictMap] , {}
     * @property {boolean} [prevent=true] 
     */

    /**
     *  el-form 
     * @event validate
     * @param {*} value  prop 
     */

    props: {
      // 
      model: Object,
      // 
      itemWidth: String,
      // 
      footer: {
        type: Boolean,
        default: true
      },
      // 
      footerAlign: {
        type: String,
        validator(val) {
          return ['', 'left', 'right', 'center'].includes(val)
        }
      },
      // false 
      submitText: {
        type: [String, Boolean],
        default: ''
      },
      // 
      submittingText: {
        type: String,
        default: '...'
      },
      // Promise
      onSubmit: {
        type: Function
      },
      // false 
      resetText: {
        type: [String, Boolean],
        default: ''
      },
      // 
      resetSubmitSuccess: Boolean,

      // 
      collapsible: Boolean,

      // 
      collapsed: {
        type: Boolean,
        default: true
      },
      // transition  name
      collapseEffect: String,

      // 
      resetCollapsed: Boolean,

      // 
      footerBlock: Boolean,

      // 
      footerExpandBlock: {
        type: Boolean,
        default: true
      },
      // 
      footerFloat: Boolean,
      // , Promise, loader
      loader: Function,

      // 
      dictMap: Object,

      // 
      prevent: {
        type: Boolean,
        default: true
      }
    },
    data() {
      // 
      this.items = {}
      return {
        currentModel: {},
        // 
        submitting: false,
        // 
        currentCollapsed: true
      }
    },
    computed: {
      footerClass() {
        return {
          [`is-align-${this.footerAlign}`]: !!this.footerAlign,
          'is-block': this.footerBlock || (!this.currentCollapsed && this.footerExpandBlock),
          'is-float-right': this.footerFloat
        }
      }
    },
    watch: {
      model: {
        immediate: true,
        deep: true,
        handler(val) {
          // 
          if (!isEqual(val, this.currentModel)) {
            this.currentModel = cloneDeep(val || {})
          }
        }
      },
      collapsed: {
        immediate: true,
        handler(val) {
          this.currentCollapsed = val
        }
      },
      currentModel: {
        deep: true,
        handler(val) {
          // if (isEqual(val, old)) return
          /**
           * 
           * @event change
           * @param {Object} model 
           */
          this.triggerChange(val)
        }
      },
      dictMap(val) {
        if (!val) return
        Object.keys(this.items).forEach(name => {
          const vm = this.items[name]
          const {dict, loader, options} = vm
          //  options  loader, 
          if (!options && !loader && dict) {
            vm.currentOptions = val[dict] || []
          }

        })
      }
    },
    methods: {
      /**
       * 
       * @method submit
       */
      submit() {
        this.$refs.elForm.validate(valid => {
          if (valid) {
            const model = cloneDeep(this.currentModel)
            if (this.onSubmit) {
              this.submitting = true
              this.onSubmit(model, this)
                .then(() => {
                  // 
                  this.resetSubmitSuccess && this.reset()
                })
                .finally(() => {
                  this.submitting = false
                })
            }
            /**
             * 
             * @event submit
             * @param {object} model 
             * @param {VueComponent} vm 
             */
            this.$emit('submit', model, this)
          }
        })
      },
      /**
       * 
       * @method reset
       */
      reset() {
        this.currentModel = cloneDeep(this.getDefaultValue())
        this.$nextTick(() => {
          this.$refs.elForm && this.$refs.elForm.clearValidate()
          /**
           * 
           * @event reset
           * @param {VueComponent} vm 
           */
          this.$emit('reset', this)
        })
      },
      toggleCollapsed() {
        this.currentCollapsed = !this.currentCollapsed
        this.$emit('collapse', this.currentCollapsed, this)
      },
      addItem(vm) {
        const {name} = vm
        if (!name) return
        if (name in this.items) {
          throw new Error(` name${name} `)
        }
        this.items[name] = vm
      },
      removeItem(vm) {
        if (!vm.name) return
        delete this.items[vm.name]
      },
      getDefaultValue() {
        const values = {}
        Object.entries(this.items).forEach(([name, item]) => {
          values[name] = item.getDefaultValue()
          // _set(values, name, item.getDefaultValue())
        })
        return {...values, ...(this.model || {})}
      },
      triggerChange(val) {
        clearTimeout(this.changeTimer)
        this.changeTimer = setTimeout(() => {
          /**
           * 
           * @event change
           * @param {Object} model 
           */
          this.$emit('change', cloneDeep(val))
        }, 100)

      }
    }
  }
</script>
