import Vue from 'vue'
import {install} from '$ui/utils/helper'
import Module from './src/Dialog'

/**
 * 
 * @param {Object} options MyDialog 
 * @param {VNode|string} vnode DOM
 * @return {*}
 */
Module.create = function (options, vnode) {
  const vm = new Vue({
    render(h) {
      return h(Module, {
        ref: 'dialog',
        props: options
      }, [vnode])
    }
  })
  vm.$mount()
  return vm.$refs.dialog
}


export default install(Module)
