<template>
  <div class="my-table" :class="classes">
    <MySpin v-bind="spin" fit :loading="isPager && currentLoading">
      <Toolbar v-if="toolbar" ref="toolbar" :title="title" :size="$attrs.size">
        <slot name="title" slot="title"></slot>
        <slot name="actions" slot="actions"></slot>
      </Toolbar>
      <div class="my-table__wrapper" :style="{height:tableHeight}">
        <slot name="prepend"></slot>
        <ElTable ref="elTable"
                 v-bind="$attrs"
                 v-on="$listeners"
                 :data="isVirtual ? viewItems : list"
                 :height="tableHeight ? '100%' : null">
          <TableColumn v-for="(col, index) in displayColumns"
                       :key="`${col.prop}_${col.type}_${index}`"
                       v-bind="col"
                       :class-name="_defineColClass(col)"
                       >
            <template v-if="col.type==='handle' || ( col.prop && $scopedSlots[col.prop])" v-slot="scope">
              <!--  -->
              <slot :name="col.prop || '_handle'" v-bind="scope" :column="col" :startIndex="startIndex"></slot>
            </template>

            <template v-if="col.type==='handle' || (col.prop && $scopedSlots[`${col.prop}.header`])"
                      v-slot:header="scope">
              <!--  -->
              <slot :name="`${col.prop||'_handle'}.header`" v-bind="{column:col, $index:index, ...scope}">
                {{col.label}}
              </slot>
            </template>

          </TableColumn>

          <TableColumn v-if="columnFilter" :resizable="false" width="24px" class-name="my-table--not-drag" fixed="right">
            <ColumnFilter slot="header" :columns="columns" v-model="displayColumnProps"></ColumnFilter>
          </TableColumn>

          <template v-slot:append>
            <!--  -->
            <slot name="append"></slot>
            <!-- loading -->
            <div v-if="isScroll && currentLoading" class="my-table__skeleton">
              <slot name="skeleton">...</slot>
            </div>

            <div v-if="isScroll && !currentLoading && isNoMore" class="my-table__complete">
              <!--  -->
              <slot name="complete"></slot>
            </div>
          </template>

          <!--  -->
          <slot name="empty" slot="empty"></slot>

        </ElTable>
      </div>
      <Pagination v-if="isPager && pagination!==null && currentTotal"
                  ref="pager"
                  v-bind="pagerProps"
                  :total="currentTotal"
                  :currentPage="currentPage"
                  class="my-table__pager"
                  @size-change="handlePageSizeChange"
                  @current-change="handlePageChange"></Pagination>
    </MySpin>
  </div>
</template>

<script>
  /**
   * , el-table
   * @module $ui/components/my-table
   */


  import {Table, TableColumn, Pagination} from 'element-ui'
  import {getStyle} from 'element-ui/lib/utils/dom'
  import {MySpin} from '$ui'
  import Pager from './Pager'
  import Scroll from './Scroll'
  import Virtual from './Virtual'
  import Toolbar from './Toolbar'
  import ColumnFilter from './ColumnFilter';
  import Sortable from 'sortablejs'
  import {insertAfter} from '$ui/utils/dom'

  /**
   * 
   * @member slots
   * @property {string} title 
   * @property {string} actions 
   * @property {string} toolbar toolbar titleactions
   * @property {string} prepend 
   * @property {string} append el-table append 
   * @property {string} empty el-table empty 
   * @property {string} $prop $propel-table-columnpropel-table-column
   * @property {string} $prop.header  el-table-columnheader
   * @property {string} skeleton loading
   * @property {string} complete 
   */

  export default {
    name: 'MyTable',
    mixins: [Pager, Scroll, Virtual],
    components: {
      ElTable: Table,
      TableColumn: TableColumn,
      Pagination: Pagination,
      MySpin,
      Toolbar,
      ColumnFilter
    },
    /**
     * 
     * @member props
     * @property {string} [title] 
     * @property {boolean} [toolbar] toolbar title
     * @property {boolean} [headerBackground] 
     * @property {Array} [data] data  loader 
     * @property {Array} [columns] el-table-column display truecolumnFilter=true prop
     * @property {boolean} [columnFilter] 
     * @property {string} [mode=pager]  'pager', 'scroll', 'virtual'
     * @property {boolean} [loading] 
     * @property {Object} [spin] MySpin
     * @property {boolean} [fit] 
     * @property {number} [page=1] 
     * @property {number} [pageSize=10] 
     * @property {number} [total=0] 
     * @property {Object} [pagination] el-pagination
     * @property [Function] [loader] pagepageSize, Promise
     * @property {boolean} [auto] loaderfalse
     * @property {number} [scrollDelay=200] ms
     * @property {number} [scrollDistance=0] px
     * @property {number} [itemHeight] 
     * @property {Object | Boolean} [columnSortable = false] , Sortable
     * @property {Object | Boolean} [rowSortable = false] , Sortable
     */

    props: {
      // 
      title: String,
      // 
      toolbar: Boolean,
      // 
      headerBackground: Boolean,
      // 
      data: {
        type: Array,
        default() {
          return []
        }
      },
      // 
      columns: {
        type: Array,
        default() {
          return []
        }
      },
      // 
      columnFilter: Boolean,

      // 
      mode: {
        type: String,
        default: 'pager',
        validator(val) {
          return ['pager', 'scroll', 'virtual'].includes(val)
        }
      },
      // loading
      loading: Boolean,

      // loading
      spin: {
        type: Object,
        default() {
          return {
            tip: '...'
          }
        }
      },

      // 
      fit: Boolean,

      // 
      page: {
        type: Number,
        default: 1
      },

      // 
      pageSize: {
        type: Number,
        default: 10
      },
      // 
      total: {
        type: Number,
        default: 0
      },
      // 
      pagination: {
        type: [Object, Boolean],
        default() {
          return {
            background: true,
            layout: 'total, ->, prev, pager, next'
          }
        }
      },
      // 
      loader: Function,

      // 
      auto: {
        type: Boolean,
        default: true
      },
      // ms
      scrollDelay: {
        type: Number,
        default: 200
      },
      // px
      scrollDistance: {
        type: Number,
        default: 0
      },
      //  size
      itemHeight: Number,

      // 
      columnSortable: {
        type: [Object, Boolean],
        default: false
      },
      // 
      rowSortable: {
        type: [Object, Boolean],
        default: false
      }
    },
    data() {
      return {
        list: [],
        currentPage: this.page,
        currentTotal: this.total,
        currentPageSize: this.pageSize,
        errorMessage: null,
        toolbarHeight: 0,
        pagerHeight: 0,
        currentLoading: this.loading,
        displayColumnProps: [],

        // Sortable
        columnSortableInstance: null,
        columnsProxy: [...this.columns]
      }
    },
    computed: {
      classes() {
        return {
          'is-fit': this.fit,
          [`my-table--${this.mode}`]: !!this.mode,
          'my-table--header-bg': this.headerBackground
        }
      },
      tableHeight() {
        if (!this.fit) return null
        return `calc(100% - ${this.toolbarHeight + this.pagerHeight}px)`
      },
      displayColumns() {
        return this.columnsProxy.filter(col => {
          // type  
          if (col.type || !col.prop) return true
          return this.displayColumnProps.includes(col.prop)
        })
      }
    },
    watch: {
      loading: {
        immediate: true,
        handler(val) {
          this.currentLoading = val
        }
      },
      // 
      data: {
        immediate: true,
        handler(val) {
          this.list = Object.freeze(val.slice(0))
        }
      },
      columns: {
        immediate: true,
        handler() {
          this.columnsProxy = [...this.columns]
          this.resetDisplayColumns()
        }
      },
      list() {
        this.startRowIndex = 0
        this.startOffset = 0
        this.$nextTick(() => {
          this.updateView()
          this.scrollTop()
        })
      },
      displayColumnProps(val) {
        /**
         * 
         * @event column-change
         * @param {String[]} columnPropNames
         */
        this.$emit('column-change', val)
      }
    },
    methods: {
      resetDisplayColumns() {
        this.displayColumnProps = this.columnsProxy.filter(col => {
          if (!col.prop || col.type) return false
          return col.display !== false
        }).map(col => col.prop)
      },
      updateView() {
        if (this.$refs.toolbar) {
          const el = this.$refs.toolbar.$el
          const marginBottom = parseInt(getStyle(el, 'margin-bottom')) || 0
          this.toolbarHeight = el.getBoundingClientRect().height + marginBottom
        }

        if (this.$refs.pager) {
          this.pagerHeight = this.$refs.pager.$el.getBoundingClientRect().height
        }
      },
      load() {
        if (!this.loader) return
        const loaders = {
          pager: this.pagerLoaded,
          scroll: this.scrollLoaded,
          virtual: this.virtualLoaded
        }
        this.currentLoading = true
        this.errorMessage = null
        this.loader(this.currentPage, this.currentPageSize).then(res => {
          const loaded = loaders[this.mode]
          loaded && loaded(res)
          this.currentTotal = res.total || 0
          /**
           * 
           * @event success
           * @param {object} [res] 
           */
          this.$emit('success', res)
        }).catch(e => {
          this.errorMessage = e;
          /**
           * 
           * @event error
           * @param {*} e 
           */
          this.$emit('error', e)
        }).finally(() => {
          this.currentLoading = false
        })
      },
      /**
       * 
       * @method refresh
       * @param {number} [page=1] 
       */
      refresh(page) {
        this.currentPage = page || this.currentPage
        this.load()
      },

      // el-table
      extendMethods(ref, names = []) {
        if (!ref) return

        names.forEach(name => {
          // 
          this[name] = (...args) => {
            ref[name].apply(ref, args)
          }
        })

      },
      /*
       *  sortTable
       */
      initRowSortable() {
        const tbody = this.$refs.elTable.$refs.bodyWrapper.getElementsByTagName('tbody')[0]
        if (!tbody) return

        const options = Object.assign({
          forceFallback: false,
          animation: 150
        }, this.rowSortable, {
          onSort: (e) => {
            this.rowSort(tbody, e)
            this.$nextTick(() => {
              const showList = this.isVirtual ? this.viewItems : this.list
              /**
               * 
               * @event on-row-sort
               * @param {*} e
               * @param {array} displayRows
               * @param {array} rows
               */
              this.$emit('on-row-sort', e, showList, this.list)
            })

          }
        })

        this.rowSortableInstance = new Sortable(tbody, options)
      },
      rowSort(container, e) {
        const currentList = this.isVirtual ? this.viewItems : this.list
        const totalList = [...this.list]
        const tempData = [...currentList]
        let realOldIndex, realNewIndex
        if (this.isVirtual) {
          realOldIndex = this.startIndex + e.oldIndex
          realNewIndex = this.startIndex + e.newIndex
        } else {
          realOldIndex = e.oldIndex
          realNewIndex = e.newIndex
        }
        const temp = tempData[e.oldIndex]
        this._handleSortDom(container, e)

        totalList.splice(realOldIndex, 1)
        totalList.splice(realNewIndex, 0, temp)

        this.list = []
        this.$nextTick(() => {
          this.list = totalList
        })
      },
      /*
       *  sortTable
       */
      initColumnSortable() {
        const tr = this.$refs.elTable.$refs.tableHeader.$el.getElementsByTagName('tr')[0]
        if (!tr) return

        const options = Object.assign({
          forceFallback: false,
          animation: 150
        }, this.columnSortable, {
          handle: '.cell',
          filter: '.my-table--not-drag',
          onSort: (e) => {
            this.colSort(tr, e)
            this.$nextTick(() => {
              /**
               * 
               * @event on-column-sort
               * @param {*} e
               * @param {array} displayColumns
               * @param {array} allColumns
               */
              this.$emit('on-column-sort', e, this.displayColumns, this.columnsProxy)
            })
          }
        })

        this.columnSortableInstance = new Sortable(tr, options)

      },
      /**
       * SortableDOMDOM
       * @function
       * @private
       * @param {HTMLElement} container DOM
       * @param {Object} e Sortable
       */
      colSort(container, e) {
        const tempData = [...this.columnsProxy]
        const oldCol = this.displayColumns[e.oldIndex]
        const newCol = this.displayColumns[e.newIndex]

        const realOldIndex = this.columnsProxy.findIndex((item) => {
          return item.prop === oldCol.prop
        })

        const realNewIndex = this.columnsProxy.findIndex((item) => {
          return item.prop === newCol.prop
        })

        this._handleSortDom(container, e)

        tempData.splice(realOldIndex, 1)
        tempData.splice(realNewIndex, 0, oldCol)

        this.columnsProxy = []
        this.$nextTick(() => {
          this.columnsProxy = tempData
        })
      },

      _handleSortDom(container, e) {
        container.removeChild(e.item)
        const children = container.children
        if (children.length > 0) {
          e.oldIndex > 0
            ? insertAfter(e.item, children[e.oldIndex - 1])
            : container.insertBefore(e.item, children[0])
        } else {
          container.appendChild(e.item)
        }
      },
      _defineColClass(col) {
        if (col.type === 'expand' || col.fixed) {
          return 'my-table--not-drag'
        } else {
          return ''
        }
      }
    },
    mounted() {
      this.updateView()
      // el-table
      this.extendMethods(this.$refs.elTable, [
        'clearSelection',
        'toggleRowSelection',
        'toggleAllSelection',
        'toggleRowExpansion',
        'setCurrentRow',
        'clearSort',
        'clearFilter',
        'doLayout',
        'sort'
      ])

      this.columnSortable && this.initColumnSortable()
      this.rowSortable && this.initRowSortable()

    },
    beforeDestroy() {
      this.columnSortableInstance && this.columnSortableInstance.destroy()
      this.rowSortableInstance && this.rowSortableInstance.destroy()
    }
  }
</script>
