<template>
  <div class="my-empty" :class="classes">
    <div class="my-empty__body">
      <slot>
        <div class="my-empty__img"><img :src="image"/></div>
        <p class="my-empty__tip">{{tip}}</p>
      </slot>
    </div>
  </div>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-empty
   */

  const IMAGE_SIMPLE = require('$ui/assets/img/PRESENTED_IMAGE_SIMPLE.png')
  const IMAGE_DEFAULT = require('$ui/assets/img/PRESENTED_IMAGE_DEFAULT.png')

  /**
   * 
   * @member slots
   * @property {string} default 
   */
  export default {
    IMAGE_SIMPLE,
    IMAGE_DEFAULT,
    name: 'MyEmpty',
    /**
     * 
     * @member props
     * @property {String} [tip=] 
     * @property {String} [image=MyEmpty.IMAGE_DEFAULT]  MyEmpty.IMAGE_DEFAULT  MyEmpty.IMAGE_SIMPLE
     * @property {Boolean} [fit] 
     */
    props: {
      tip: {
        type: String,
        default: ''
      },
      image: {
        type: String,
        default: IMAGE_DEFAULT
      },
      fit: Boolean
    },
    computed: {
      classes() {
        return {
          'is-fit': this.fit
        }
      }
    }
  }
</script>
