<template>
  <MyForm class="my-filter"
          :class="classes"
          v-bind="formOptions"
          v-on="$listeners"
          :item-width="itemWidth">
    <slot></slot>
  </MyForm>
</template>

<script>
  /**
   * , MyForm
   * @module $ui/components/my-filter
   */

  import {MyForm} from '$ui'
  import responsive, {responsiveArray} from '$ui/utils/responsive'

  // 
  const defaultValues = {
    inline: true,
    footerAlign: 'right',
    submitText: '',
    size: 'small',
    footerExpandBlock: false,
    footerFloat: false
  }

  /**
   * 
   * @member slots
   * @property {string} default 
   */
  export default {
    name: 'MyFilter',
    components: {
      MyForm
    },
    /**
     * MyFrom
     * @member props
     * @property {number|object} [columns] , 
     *
     */
    props: {
      // 
      columns: {
        type: [Number, Object],
        default() {
          return {
            xs: 1,
            sm: 2,
            md: 2,
            lg: 3,
            xl: 3,
            xxl: 4
          }
        }
      }
    },
    data() {
      return {
        // 
        screens: {},
        currentColumn: 1
      }
    },
    computed: {
      classes() {
        return {
          [`is-${this.$attrs.size}`]: !!this.$attrs.size
        }
      },
      itemWidth() {
        return `calc(${100 / this.currentColumn}% - 10px)`
      },
      formOptions() {
        return {
          ...defaultValues,
          ...this.$attrs
        }
      }
    },
    watch: {
      columns: {
        immediate: true,
        handler() {
          this.setupResponsive()
          this.currentColumn = this.getResponsiveValue()
        }
      },
      screens() {
        this.currentColumn = this.getResponsiveValue()
      }
    },
    methods: {
      // 
      setupResponsive() {
        this.token && responsive.off(this.token)

        // 
        if (typeof this.columns !== 'object') return

        this.token = responsive.on(screens => {
          this.screens = screens
        })
      },
      // 
      getResponsiveValue() {
        const columns = this.columns
        const defaultValue = 1
        if (!columns) return defaultValue
        // 
        if (typeof columns === 'object') {
          for (let i = 0; i < responsiveArray.length; i++) {
            const breakpoint = responsiveArray[i]
            if (this.screens[breakpoint]) {
              return columns[breakpoint] || defaultValue
            }
          }
        }

        // 
        return columns

      }
    },
    beforeDestroy() {
      this.token && responsive.off(this.token)
    }
  }
</script>
