
  <template>
  <Card class="my-panel" :shadow="shadow" :class="classes" :bodyStyle="cardBodyStyle">
    <template v-if="header" v-slot:header>
      <div v-if="tabs" class="my-panel__header my-panel__tabs" ref="header" :style="headerStyle">
        <Tabs v-model="currentTab">
          <TabPane v-for="tab in tabs" :key="tab.name||tab.label" v-bind="tab" lazy>
            <slot v-if="$scopedSlots.label" name="label" slot="label" v-bind="tab"></slot>
          </TabPane>
        </Tabs>
        <div v-if="$slots.handle" class="my-panel__handle">
          <slot name="handle"></slot>
        </div>
      </div>
      <div v-else class="my-panel__header" ref="header" :style="headerStyle">
        <slot name="header">
          <my-header :title="title"
                     :icon="icon"
                     :theme="titleTheme"
                     :size="size"
                     :background="titleBackground">
            <template v-slot:title>
              <slot name="title"></slot>
            </template>
            <template v-slot:handle>
              <slot name="handle"></slot>
            </template>
          </my-header>
        </slot>
      </div>
    </template>

    <div class="my-panel__body"
         :class="{'is-fit':fit}"
         :style="mergeBodyStyle">
      <slot></slot>

      <template v-for="tab in tabs">
        <slot v-if="tab.name===currentTab" :name="tab.name" v-bind="tab"></slot>
      </template>
    </div>

    <div v-if="$slots.footer"
         ref="footer"
         class="my-panel__footer"
         :class="[`is-${footerAlign}`, `is-${theme}`]"
         :style="footerStyle">
      <slot name="footer"></slot>
    </div>
    <div v-if="actions && actions.length" class="my-panel__actions">
      <Action v-for="(action,index) in actions"
              :key="index" v-bind="action"
              :style="{width:`${100/actions.length}%`}"
              @click.native="handleActionClick(action)">
        <slot v-if="$scopedSlots.action" name="action" v-bind="action"></slot>
      </Action>
    </div>

  </Card>
</template>

<script>
  
     import __vue__ from 'vue';
     
     import {MyHeader as __MyHeader__} from '$ui';
     
     
     
     /**
   * 
   * @module $ui/components/my-panel
   */

  import {Card, Tabs, TabPane} from 'element-ui'

  import {MyHeader} from '$ui'
  import {addResizeListener, removeResizeListener} from 'element-ui/lib/utils/resize-event'
  import {throttle} from '$ui/utils/util'
  import Action from './Action'

  /**
   * 
   * @member slots
   * @property {string} default 
   * @property {string} header title  handle 
   * @property {string} title 
   * @property {string} handle 
   * @property {string} footer 
   * @property {string} label tablabel
   */

  export default {
    name: 'MyPanel',
    components: {
      Card,
      MyHeader,
      Tabs,
      TabPane,
      Action
    },
    /**
     * 
     * @member props
     * @property {boolean} [header=true] 
     * @property {string} [shadow=always] 'always', 'hover', 'never'
     * @property {string} [title] title
     * @property {string} [icon] 
     * @property {string} [theme] '', 'background', 'border-top', 'border-left', 'flag'
     * @property {string} [size] '', 'large', 'medium', 'small'
     * @property {boolean} [fit=false] 
     * @property {boolean} [border=true] 
     * @property {string} [footerAlign=right] , 'left', 'center', 'right'
     * @property {Object} [bodyStyle] 
     * @property {Object} [headerStyle] 
     * @property {Object} [footerStyle] 
     * @property {Array} [tabs]  {label, name, disabled}, name
     * @property {String} [defaultTab] tab 
     * @property {Array} [actions]   {text, icon}
     */
    props: {
      // 
      header: {
        type: Boolean,
        default: true
      },
      // 
      shadow: {
        type: String,
        default: 'always',
        validator(val) {
          return ['always', 'hover', 'never'].includes(val)
        }
      },
      // 
      title: String,
      //  icon
      icon: String,

      // 
      theme: {
        type: String,
        validator() {
          return ['', 'background', 'border-top', 'border-left', 'flag']
        }
      },
      // 
      size: String,

      // 
      fit: Boolean,
      // body
      bodyStyle: Object,
      // 
      footerStyle: Object,
      // 
      headerStyle: Object,
      // 
      footerAlign: {
        type: String,
        default: 'right',
        validator(val) {
          return ['left', 'center', 'right'].includes(val)
        }
      },
      // 
      border: {
        type: Boolean,
        default: true
      },
      tabs: Array,
      defaultTab: String,
      // 
      actions: Array
    },
    data() {
      return {
        headerHeight: 0,
        footerHeight: 0,
        currentTab: this.defaultTab || (this.tabs && this.tabs[0] || {}).name
      }
    },
    computed: {
      titleBackground() {
        return this.theme === 'background'
      },
      titleTheme() {
        if (this.theme === 'flag') {
          return 'bg-down'
        }
        if (this.theme === 'border-left') {
          return 'border-left'
        }
        return null
      },
      classes() {
        return {
          [`my-panel--${this.theme}`]: !!this.theme,
          'is-fit': this.fit,
          'is-no-border': !this.border,
          [`is-${this.size}`]: !!this.size
        }
      },
      mergeBodyStyle() {
        if (!this.fit) {
          return this.bodyStyle
        }
        return {
          ...this.bodyStyle,
          height: `calc(100% - ${this.footerHeight}px)`
        }
      },
      cardBodyStyle() {
        if (!this.fit) {
          return null
        }
        return {
          height: `calc(100% - ${this.headerHeight}px)`
        }
      }
    },
    watch: {
      currentTab(val) {
        /**
         * tab
         * @event tab-change
         * @param {string} name 
         */
        this.$emit('tab-change', val)
      }
    },
    methods: {
      setHeight() {
        if (this.$refs.header) {
          this.headerHeight = this.$refs.header.getBoundingClientRect().height
        } else {
          this.headerHeight = 0
        }
        if (this.$refs.footer) {
          this.footerHeight = this.$refs.footer.getBoundingClientRect().height
        } else {
          this.footerHeight = 0;
        }
      },
      bindResize() {
        if (this.$refs.header) {
          addResizeListener(this.$refs.header, this.proxyResize)
        }
        if (this.$refs.footer) {
          addResizeListener(this.$refs.footer, this.proxyResize)
        }
      },
      unbindResize() {
        if (this.$refs.header) {
          removeResizeListener(this.$refs.header, this.proxyResize)
        }
        if (this.$refs.footer) {
          removeResizeListener(this.$refs.footer, this.proxyResize)
        }
      },
      handleActionClick(action) {
        /**
         * 
         * @event action
         * @param {object} action 
         */
        this.$emit('action', action)
      }
    },
    created() {
      this.proxyResize = throttle(this.setHeight, this)
    },
    updated() {
      this.$nextTick(this.proxyResize)
    },
    mounted() {
      this.bindResize()
      this.$nextTick(this.proxyResize)
    },
    beforeDestroy() {
      this.unbindResize()
    }
  }
     
     __vue__.use(__MyHeader__)
    
  </script>



  