<script>
  /**
   * 
   * @module $ui/components/my-contextmenu
   */
  import {Contextmenu, ContextmenuGroup, ContextmenuSubmenu, ContextmenuItem} from 'v-contextmenu'
  import {MyIcon} from '$ui'

  /**
   * 
   * @member slots
   * @property {string} default  item
   */

  /**
   * 
   * @event show
   */

  /**
   * 
   * @event hide
   */

  /**
   * 
   * @event contextmenu
   * @param {VNode} vnode 
   */

  export default {
    name: 'MyContextmenu',
    components: {
      Contextmenu,
      ContextmenuSubmenu,
      ContextmenuGroup,
      ContextmenuItem,
      MyIcon
    },
    /**
     * 
     * @member props
     * @property {string} [eventType=contextmenu] 
     * @property {string} [theme=default]  'default', 'dark', 'bright'
     * @property {boolean} [disabled] 
     * @property {boolean} [autoPlacement=true] 
     * @property {Array} [data] 
     * @property {string} [data.title] 
     * @property {string|Object} [data.icon] 
     * @property {boolean} [data.divider] 
     * @property {boolean} [data.disabled] 
     * @property {boolean} [data.autoHide=true] 
     * @property {number} [data.maxWidth] group=true
     * @property {Array} [data.group] 
     * @property {Array} [data.children] 
     *
     */
    props: {
      // 
      eventType: {
        type: String,
        default: 'contextmenu'
      },
      // 
      theme: {
        type: String,
        default: 'default',
        validator(val) {
          return ['default', 'dark', 'bright'].includes(val)
        }
      },
      // disabled
      disabled: {
        type: Boolean
      },
      // 
      autoPlacement: {
        type: Boolean,
        default: true
      },
      // [{divider, title, icon, disabled, autoHide, maxWidth, group:[],children:[]}]
      data: Array
    },
    methods: {
      createIcon(icon) {
        if (!icon) return null
        const iconProps = typeof icon === 'object' ? icon : {name: icon}
        return (<MyIcon class="my-contextmenu__placeholder" {...{props: iconProps}}></MyIcon>)
      },
      createItems(items = []) {
        const hasIcon = !!items.find(item => item.icon)
        return items.map(item => {
          if (item.group) {
            return (
              <ContextmenuGroup {...{props: item}}>
                {this.createItems(item.group)}
              </ContextmenuGroup>
            )
          } else if (item.children) {
            return (
              <ContextmenuSubmenu {...{
                props: item,
                on: {
                  mouseenter: this.handleMouseEnter.bind(this, item),
                  mouseleave: this.handleMouseLeave.bind(this, item)
                }
              }}>
                <template slot="title">
                  {
                    [
                      this.createIcon(item.icon),
                      (hasIcon && !item.icon) ? (<i class="my-contextmenu__placeholder"></i>) : null,
                      item.title
                    ]
                  }
                </template>
                {this.createItems(item.children)}
              </ContextmenuSubmenu>
            )
          } else {
            const scopedSlots = item => {
              return this.$scopedSlots.default
                ? this.$scopedSlots.default({
                  item: item
                })
                : [
                  this.createIcon(item.icon),
                  (hasIcon && !item.icon) ? (<i class="my-contextmenu__placeholder"></i>) : null,
                  item.title
                ]
            }
            return (
              <ContextmenuItem {...{
                props: item,
                on: {
                  click: this.handleClick.bind(this, item),
                  mouseenter: this.handleMouseEnter.bind(this, item),
                  mouseleave: this.handleMouseLeave.bind(this, item)
                }
              }}>
                {
                  scopedSlots(item)
                }
              </ContextmenuItem>
            )
          }
        })
      },
      // 
      extendMethods(ref, names = []) {
        if (!ref) return

        names.forEach(name => {
          // 
          this[name] = (...args) => {
            ref[name].apply(ref, args)
          }
        })
      },
      handleClick(item, vm, e) {
        /**
         * 
         * @event click
         * @param {object} 
         */
        this.$emit('click', item, vm, e)
      },
      handleMouseEnter(item, vm, e) {
        /**
         * 
         * @event mouseenter
         * @param {object} 
         */
        this.$emit('mouseenter', item, vm, e)
      },
      handleMouseLeave(item, vm, e) {
        /**
         * 
         * @event mouseenter
         * @param {object} 
         */
        this.$emit('mouseleave', item, vm, e)
      }
    },
    render() {
      const props = {
        eventType: this.eventType,
        theme: this.theme,
        disabled: this.disabled,
        autoPlacement: this.autoPlacement

      }
      return (
        <Contextmenu {...{on: this.$listeners, props: props}} ref="contextmenu" class="my-contextmenu">
          {this.createItems(this.data)}
        </Contextmenu>
      )
    },
    mounted() {
      this.extendMethods(this.$refs.contextmenu, ['show', 'hide', 'hideAll'])
    }
  }
</script>

