<template>
  <div :class="classes" v-on="$listeners">
    <MyAvatar v-for="(item, index) in list"
              :key="index"
              v-on="createEvents(item)"
              v-bind="mergeOptions(item)">{{item.text}}
    </MyAvatar>
    <MyAvatar v-if="count>0"
              theme="warning"
              :size="size"
              v-on="createClickMoreEvents()"
              :shape="shape">+{{count}}
    </MyAvatar>

    <MyAvatar v-if="addable"
              class="my-avatars__add"
              theme="info"
              :size="size"
              @click="handleClickAdd"
              :icon="addIcon"
              :shape="shape">
    </MyAvatar>

  </div>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-avatars
   */
  import {MyAvatar} from '$ui'

  export default {
    name: 'MyAvatars',
    components: {
      MyAvatar
    },
    /**
     * 
     * @member props
     * @property {Array} [data] {text, icon, src, tooltip, theme, alt}
     * @property {Object} [tooltip]  tooltip 
     * @property {number} [max] 
     * @property {string|number}  'large', 'small', 'default'
     * @property {string} [alt] 
     * @property {string} [theme]  
     * @property {string} [shape]   'circle', 'square'
     * @property {boolean} [addable] 
     * @property {string} [addIcon] 
     */
    props: {
      //  {text, icon, src, tooltip, theme, alt}
      data: {
        type: Array,
        default() {
          return []
        }
      },
      tooltip: Object,
      max: {
        type: Number,
        default: Infinity
      },
      // 
      size: {
        type: [String, Number],
        default: 'default',
        validator(val) {
          return ['large', 'small', 'default'].includes(val) || val > 0
        }
      },
      // 
      alt: String,
      // 
      theme: {
        type: String,
        default: 'primary',
        validator(val) {
          return ['primary', 'success', 'warning', 'danger', 'info'].includes(val)
        }
      },
      // 
      shape: {
        type: String,
        default: 'circle',
        validator(val) {
          return ['circle', 'square'].includes(val)
        }
      },
      // 
      addable: Boolean,

      // 
      addIcon: {
        type: String,
        default: 'el-icon-plus'
      }
    },
    computed: {
      list() {
        if (this.data.length > this.max) {
          return this.data.slice(0, this.max)
        }
        return this.data
      },
      count() {
        return this.max !== Infinity ? this.data.length - this.max : 0
      },
      classes() {
        return {
          'my-avatars': true,
          'is-pointer': !!this.$listeners.click
        }
      }
    },
    methods: {
      mergeOptions(item) {
        const tooltip = item.tooltip ? {...this.tooltip, content: item.tooltip} : undefined
        return {
          size: this.size,
          theme: this.theme,
          alt: this.alt,
          shape: this.shape,
          ...item,
          tooltip: tooltip
        }
      },
      createEvents(item) {
        return this.$listeners['click-item']
          ? {
            click: e => this.handleClickItem(item, e)
          }
          : {}
      },
      createClickMoreEvents() {
        return this.$listeners['click-more']
          ? {
            click: e => {
              /**
               * 
               * @event click-more
               */
              this.$emit('click-more', e)
            }
          }
          : {}
      },
      handleClickItem(item) {
        /**
         * 
         * @event click-item
         * @param {Object} item 
         */
        this.$emit('click-item', item)
      },
      handleClickAdd() {
        /**
         * 
         * @event click-add
         */
        this.$emit('click-add')
      }
    }
  }
</script>

