<template>
  <MyLayout class="my-crud" :north="{height:'auto'}" split :fit="fit">
    <template v-if="$slots.filter" v-slot:north>
      <MyContainer>
        <MyFilter v-bind="filter"
                  :model="currentQuery"
                  @submit="handleFilterSubmit"
                  @reset="handleFilterReset">
          <slot name="filter"></slot>
        </MyFilter>
      </MyContainer>
    </template>

    <MyContainer :fit="fit">
      <MyTable ref="table"
               v-bind="tableProps"
               :columns="columns"
               :toolbar="!!(title||$slots.title||$slots.actions)"
               :fit="fit"
               :loader="loader"
               :title="title"
               header-background>
        <slot name="title" slot="title">{{title}}</slot>
        <slot name="toolbar" slot="actions"></slot>
        <template v-slot:_handle="{row, column, $index}">
          <Handlebar :handles="column.handles"
                     :theme="column.theme"
                     :size="tableProps.size"
                     :row="row"
                     @command="handleCommand($event, row, $index)"></Handlebar>
        </template>
        <template v-slot:_handle.header="scope">
          <slot name="handle-header" v-bind="scope">{{scope.column.label}}</slot>
        </template>

        <template v-for="col in propsSlots" v-slot:[col.prop]="scope">
          <slot :name="col.prop" v-bind="scope"></slot>
        </template>

      </MyTable>
    </MyContainer>
    <!--  -->
    <MyDialog v-if="currentRow && viewDialog!==null"
              v-bind="viewDialogProps"
              :visible.sync="viewVisible"
              target="body"
              :footer="null">
      <slot name="detail" :row="currentRow" :loading="loading">
        <Detail :data="currentRow" :loading="loading"></Detail>
      </slot>
    </MyDialog>

    <!--  -->
    <MyDialog class="my-crud-form-dialog"
              v-if="formDialog!==null"
              v-bind="formDialogProps"
              :visible.sync="formDialogVisible"
              :footer="null"
              target="body"
              :title="currentRow ? '': ''">
      <MyForm v-bind="formProps" :model="currentRow" :on-submit="handleFormSubmit">
        <slot name="form" :row="currentRow" :loading="loading"></slot>
      </MyForm>
    </MyDialog>


  </MyLayout>
</template>

<script>
  /**
   *   
   *  @module $ui/components/my-crud
   */

  import {MyLayout, MyContainer, MyFilter, MyTable, MyDialog, MyForm} from '$ui'
  import Handlebar from './Handlebar'
  import Detail from './Detail'

  // 
  const defaultPagination = {
    background: true,
    layout: 'total,sizes, ->, prev, pager, next',
    pageSizes: [10, 20, 50, 100]
  }

  // 
  const defaultViewDialog = {
    width: '80%',
    draggable: true,
    title: '',
    modal: true
  }

  // 
  const defaultFormDialog = {
    width: '80%',
    draggable: true,
    modal: true
  }

  // 
  const defaultForm = {
    footerAlign: 'right'
  }

  /**
   * 
   * @member slots
   * @property {string} filter 
   * @property {string} title 
   * @property {string} toolbar 
   * @property {string} handle-header column 
   * @property {string} detail row loading 
   * @property {string} form row nullloading 
   *
   */
  export default {
    name: 'MyCrud',
    components: {
      MyLayout,
      MyContainer,
      MyFilter,
      MyTable,
      Handlebar,
      MyDialog,
      MyForm,
      Detail
    },
    /**
     * 
     * @member props
     * @property {string} [title] 
     * @property {boolean} [fit] 
     * @property {Object} [filter]  MyFilter 
     * @property {Object} [query] 
     * @property {Object} [tableOptions]  MyTable 
     * @property {Array} [columns] el-table-column
     * @property {Function|Object} [adapter]   {fetch, add, update, remove, get, batch}
     * @property {Object} [viewDialog]  nulldialog
     * @property {Object} [formDialog] null 
     * @property {Object} [formOptions]  
     *
     */
    props: {
      title: String,
      fit: Boolean,
      // MyFilter
      filter: Object,

      // 
      query: Object,

      // 
      tableOptions: Object,

      // 
      //  handle
      //  {type:'handle',label,theme:'button|text|icon', handles:[{view,edit,remove,text,icon,type,access}]}
      columns: Array,

      // 
      adapter: {
        type: [Function, Object],
        required: true,
        default() {
          return {
            fetch: null,
            add: null,
            update: null,
            remove: null,
            get: null,
            batch: null
          }
        }
      },

      //  nulldialog
      viewDialog: {
        type: Object
      },
      // 
      formDialog: {
        type: Object
      },
      // 
      formOptions: {
        type: Object
      }
    },
    data() {
      this.dataAdapter = null
      return {
        currentQuery: this.query,
        loading: false,
        currentRow: null,
        viewVisible: false,
        formDialogVisible: false
      }
    },
    computed: {
      tableProps() {
        return {
          pagination: defaultPagination,
          ...this.tableOptions
        }
      },
      viewDialogProps() {
        return {
          ...defaultViewDialog,
          ...this.viewDialog
        }
      },
      formDialogProps() {
        return {
          ...defaultFormDialog,
          ...this.formDialog
        }
      },
      formProps() {
        return {
          ...defaultForm,
          ...this.formOptions
        }
      },
      propsSlots() {
        return (this.columns || []).filter(col => (!!col.prop && this.$scopedSlots[col.prop]))
      }
    },
    watch: {
      query: {
        immediate: true,
        handler(val) {
          this.currentQuery = val
        }
      }
    },
    methods: {
      /**
       * 
       * @method openAddDialog
       */
      openAddDialog() {
        // 
        if (!this.dataAdapter.add) return
        this.currentRow = null
        this.formDialogVisible = true
      },
      emitError(type, message) {
        /**
         * 
         * @event error
         * @param {string} type  fetch/get/update/remove/batch
         * @param {*} message 
         */
        this.$emit('error', type, message)
      },
      loader(page, limit) {
        if (!this.dataAdapter.fetch) {
          return Promise.reject(new Error('fetch'))
        }
        return this.dataAdapter.fetch({
          page,
          limit,
          ...this.currentQuery
        }, this)
          .catch(e => {
            this.emitError('fetch', e)
          })
      },
      getDetail({row, index}) {
        if (!this.dataAdapter.get) return
        this.loading = true
        this.dataAdapter.get({
          row,
          index
        })
          .then(res => {
            this.currentRow = Object.freeze({
              ...row,
              ...res
            })
          })
          .catch(e => {
            this.emitError('get', e)
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleFilterSubmit(query) {
        this.currentQuery = Object.freeze(query)
        this.refresh()
      },
      handleFilterReset() {
        this.currentQuery = Object.freeze(this.query)
      },
      handleFormSubmit(row) {
        // 
        if (this.currentRow) {
          return this.dataAdapter.update({
            row,
            index: 0
          })
            .then(() => {
              this.formDialogVisible = false
              this.currentRow = null
              this.reload()
            })
            .catch(e => {
              this.emitError('update', e)
            })
        } else {
          // 
          return this.dataAdapter.add({
            row,
            index: 0
          })
            .then(() => {
              this.formDialogVisible = false
              this.refresh()
            })
            .catch(e => {
              this.emitError('add', e)
            })
        }
      },
      handleCommand(handle, row, index) {
        this.currentRow = Object.freeze(row)

        // 
        if (handle.view) {
          this.$nextTick(() => {
            this.viewVisible = true
          })
          this.getDetail({
            row,
            index
          })
        }

        // 
        if (handle.edit) {
          this.$nextTick(() => {
            this.formDialogVisible = true
          })
          this.getDetail({
            row,
            index
          })
        }

        // 
        if (handle.remove) {
          this.$confirm('', '', {
            type: 'warning'
          })
            .then(() => {
              this.dataAdapter.remove({
                row,
                index
              })
                .then(() => {
                  this.currentRow = null
                  this.reload()
                })
                .catch(e => {
                  this.emitError('add', e)
                })
            })
            .catch(e => {
            })
        }
        /**
         * 
         * @event command
         * @param {Object} handle 
         * @param {Object} row 
         * @param {number} index 
         */
        this.$emit('command', handle, row, index)
      },
      /**
       * 
       * @method reload
       */
      reload() {
        this.$refs.table.load()
      },
      /**
       * 
       * @method refresh
       */
      refresh() {
        this.$refs.table.refresh()
      },
      batchRemove() {
        const elTable = this.$refs.table.$refs.elTable
        const {selection, rowKey} = elTable
        const rows = rowKey ? selection.map(item => item[rowKey]) : selection
        if (rows.length === 0) {
          this.$message.info('')
          return
        }
        const list = this.$refs.table.list
        const indexes = selection.map(row => list.findIndex(item => row === item))
        const message = this.$message({
          type: 'info',
          message: '...',
          duration: 0
        })
        this.dataAdapter.batch({
          rows,
          indexes
        })
          .then(this.reload)
          .catch(e => {
            this.emitError('batch', e)
          })
          .finally(() => {
            message.close()
          })
      }
    },
    created() {
      this.dataAdapter = typeof this.adapter === 'function'
        ? this.adapter(this)
        : this.adapter

    }
  }
</script>

